    composes: text_type_main-default text_color_inactive from '@ya.praktikum/react-developer-burger-ui-components/dist/ui/common.css'    ;

    composes: mt-1 mb-20 from '@ya.praktikum/react-developer-burger-ui-components/dist/ui/box.css'    ;
}



.container {
    width: 600px;
    max-height: 80vh;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    composes: p-10 from '@ya.praktikum/react-developer-burger-ui-components/dist/ui/box.css'    ;
